<template lang="html">
  <div class="">
    <ul :style="ulStyle">
      <li v-for="value in response.all_users" :key="value.user_id">{{ value.name }}</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    id_token: String
  },
  data: function() {
    return {
      response: null,
      columns: 0
    }
  },
  computed: {
    ulStyle: function() {
      return {
        columns: this.columns,
        listStyleType: 'circle'
      }
    }
  },
  mounted() {
    axios
      .get('https://hzyo7y0ukj.execute-api.eu-west-1.amazonaws.com/dev/all_users', {
        headers: {
          "Authorization": this.id_token
        }
      })
      .then(response => {
        this.response = response.data;
        this.columns = Math.floor(this.response.all_users.length/5)+1;
      })
      .catch(error => {
        this.message = error;
        this.showAlert = true;
    });
  }
}
</script>

